n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n.loading-text {\n  font-size: 16px;\n  font-weight: 500;\n  color: #333;\n  margin-bottom: 8px;\n}\n\n.loading-subtext {\n  font-size: 13px;\n  color: #666;\n}\n\n/* ========================================================================\n   ERROR STATE\n   ======================================================================== */\n\n.error-container {\n  padding: 24px;\n  text-align: center;\n}\n\n.error-icon {\n  font-size: 48px;\n  margin-bottom: 16px;\n}\n\n.error-title {\n  font-size: 18px;\n  font-weight: 600;\n  color: #d32f2f;\n  margin-bottom: 16px;\n}\n\n.error-details {\n  background-color: #fff3cd;\n  border: 1px solid #ffc107;\n  border-radius: 4px;\n  padding: 12px;\n  margin-bottom: 16px;\n  text-align: left;\n}\n\n.error-details pre {\n  font-size: 12px;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  color: #856404;\n  margin: 0;\n}\n\n/* ========================================================================\n   SUCCESS STATE\n   ======================================================================== */\n\n.success-container {\n  padding: 24px;\n  text-align: center;\n}\n\n.success-icon {\n  font-size: 64px;\n  color: #4caf50;\n  margin-bottom: 16px;\n}\n\n.success-title {\n  font-size: 20px;\n  font-weight: 600;\n  color: #4caf50;\n  margin-bottom: 16px;\n}\n\n.success-details {\n  background-color: #f1f8f4;\n  border: 1px solid #4caf50;\n  border-radius: 4px;\n  padding: 16px;\n  margin-bottom: 16px;\n  text-align: left;\n}\n\n.success-details p {\n  margin-bottom: 8px;\n}\n\n.task-id,\n.contact-id {\n  display: block;\n  background-color: #fff;\n  padding: 8px;\n  border-radius: 4px;\n  font-family: \"Courier New\", monospace;\n  font-size: 13px;\n  color: #333;\n  word-break: break-all;\n}\n\n.success-message {\n  font-size: 14px;\n  color: #666;\n  margin: 16px 0;\n}\n\n/* ========================================================================\n   EMAIL INFO SECTION\n   ======================================================================== */\n\n.email-info-section {\n  background-color: #f9f9f9;\n  border: 1px solid #e0e0e0;\n  border-radius: 6px;\n  padding: 16px;\n  margin-bottom: 24px;\n}\n\n.section-title {\n  font-size: 16px;\n  font-weight: 600;\n  color: #333;\n  margin-bottom: 12px;\n  border-bottom: 2px solid #667eea;\n  padding-bottom: 8px;\n}\n\n.email-info-grid {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.email-field {\n  display: flex;\n  flex-direction: column;\n}\n\n.email-field label {\n  font-size: 12px;\n  font-weight: 600;\n  color: #666;\n  margin-bottom: 4px;\n}\n\n.email-field span {\n  font-size: 13px;\n  color: #333;\n  word-break: break-word;\n}\n\n.email-preview {\n  font-style: italic;\n  color: #666;\n}\n\n/* ========================================================================\n   TASK FORM SECTION\n   ======================================================================== */\n\n.task-form-section {\n  margin-bottom: 24px;\n}\n\n.section-description {\n  font-size: 13px;\n  color: #666;\n  margin-bottom: 16px;\n}\n\n.task-form {\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n}\n\n.form-field {\n  display: flex;\n  flex-direction: column;\n}\n\n.form-field label {\n  font-size: 13px;\n  font-weight: 600;\n  color: #333;\n  margin-bottom: 6px;\n}\n\n.form-field input[type=\"text\"],\n.form-field input[type=\"number\"],\n.form-field select,\n.form-field textarea {\n  width: 100%;\n  padding: 10px;\n  border: 1px solid #d0d0d0;\n  border-radius: 4px;\n  font-size: 14px;\n  font-family: inherit;\n  color: #333;\n  background-color: #fff;\n  transition: border-color 0.2s, box-shadow 0.2s;\n}\n\n.form-field input[type=\"text\"]:focus,\n.form-field input[type=\"number\"]:focus,\n.form-field select:focus,\n.form-field textarea:focus {\n  outline: none;\n  border-color: #667eea;\n  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);\n}\n\