;\n  align-items: center;\n  justify-content: center;\n  z-index: 1000;\n}\n\n.creating-overlay .spinner {\n  border-color: rgba(255, 255, 255, 0.3);\n  border-top-color: white;\n}\n\n.creating-overlay p {\n  color: white;\n  font-size: 16px;\n  font-weight: 500;\n}\n\n/* ========================================================================\n   RESPONSIVE ADJUSTMENTS\n   ======================================================================== */\n\n@media (max-width: 360px) {\n  .task-pane-content {\n    padding: 12px;\n  }\n\n  .task-pane-header h1 {\n    font-size: 16px;\n  }\n\n  .btn {\n    padding: 10px 20px;\n    font-size: 13px;\n  }\n}\n", "",{"version":3,"sources":["webpack://./src/taskpane/TaskPane.css"],"names":[],"mappings":"AAAA;;;;;EAKE;;AAEF;;6EAE6E;;AAE7E;EACE,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,uGAAuG;EACvG,eAAe;EACf,gBAAgB;EAChB,WAAW;EACX,yBAAyB;AAC3B;;AAEA;;6EAE6E;;AAE7E;EACE,aAAa;EACb,sBAAsB;EACtB,iBAAiB;EACjB,sBAAsB;AACxB;;AAEA;EACE,6DAA6D;EAC7D,YAAY;EACZ,aAAa;EACb,kBAAkB;EAClB,wCAAwC;AAC1C;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,SAAS;AACX;;AAEA;EACE,OAAO;EACP,aAAa;EACb,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,eAAe;EACf,WAAW;EACX,6BAA6B;AAC/B;;AAEA;;6EAE6E;;AAE7E;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;EACvB,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;EACzB,6BAA6B;EAC7B,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,kCAAkC;EAClC,mBAAmB;AACrB;;AAEA;EACE,KAAK,uBAAuB,EAAE;EAC9B,OAAO,yBAAyB,EAAE;AACpC;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,WAAW;AACb;;AAEA;;6EAE6E;;AAE7E;EACE,aAAa;EACb,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,yBAAyB;EACzB,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,eAAe;EACf,qBAAqB;EACrB,qBAAqB;EACrB,cAAc;EACd,SAAS;AACX;;AAEA;;6EAE6E;;AAE7E;EACE,aAAa;EACb,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,cAAc;EACd,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACz