/**
 * n8n Webhook API Client
 * 
 * This module handles all communication with n8n webhooks for:
 * 1. Generating AI-powered task suggestions from email content
 * 2. Creating Salesforce tasks via n8n automation
 * 
 * CONFIGURATION:
 * TODO: Update these constants with your actual n8n webhook URLs and API key
 */

import { EmailInfo, GeneratedTask, CreateTaskPayload, CreateTaskResult } from "../types";

// ============================================================================
// CONFIGURATION - UPDATE THESE VALUES
// ============================================================================

/**
 * n8n webhook URLs - CONFIGURED
 * Generate task: https://gradhun.app.n8n.cloud/webhook-test/email-to-task
 * Task approval: https://gradhun.app.n8n.cloud/webhook-test/task-approval
 */
const N8N_BASE_URL = process.env.N8N_BASE_URL || "https://gradhun.app.n8n.cloud";

/**
 * Webhook endpoint paths
 * These are appended to N8N_BASE_URL to form the complete webhook URLs
 */
const GENERATE_TASK_PATH = "/webhook-test/email-to-task";
const CREATE_TASK_PATH = "/webhook-test/task-approval";

/**
 * API Key for n8n webhooks
 * TODO: Update this if your webhooks require authentication
 * If no authentication is needed, this header will still be sent but may be ignored
 */
const API_KEY = process.env.N8N_API_KEY || "no-key-required";

/**
 * Request timeout in milliseconds (30 seconds)
 */
const REQUEST_TIMEOUT = 30000;

// ============================================================================
// API FUNCTIONS
// ============================================================================

/**
 * Calls the n8n /generate-task webhook to get AI-generated task suggestions
 * 
 * This function sends the email information to n8n, which processes it with AI
 * and returns a suggested Salesforce task with fields pre-filled.
 * 
 * @param emailInfo - The email data extracted from Outlook
 * @returns Promise<GeneratedTask> - AI-generated task suggestion
 * @throws Error if the API call fails or returns invalid data
 */
export async function generateTaskFromEmail(emailInfo: EmailInfo): Promise<GeneratedTask> {
  const url = `${N8N_BASE_URL}${GENERATE_TASK_PATH}`;

  try {
    // Create abort controller for timeout
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), REQUEST_TIMEOUT);

    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-api-key": API_KEY,
      },
      body: JSON.stringify(emailInfo),
      signal: controller.signal,
    });

    clearTimeout(timeoutId);

    // Check if response is OK
    if (!response.ok) {
      const errorText = await response.text().catch(() => "Unknown error");
      throw new Error(`n8n webhook returned ${response.status}: ${errorText}`);
    }

    // Parse JSON response
    const data = await response.json();

    // Validate that response contains required fields
    if (!data || typeof data !== "object") {
      throw new Error("Invalid response format from n8n webhook");
    }

    // Validate required fields and provide defaults
    const generatedTask: GeneratedTask = {
      task_subject: data.task_subject || "Follow up on email",
      task_description: data.task_description || "",
      priority: validatePriority(data.priority),
      due_in_days: typeof data.due_in_days === "number" ? data.due_in_days : 3,
      category: data.category || "",
      needs_call: data.needs_call === true,
      needs_email_reply: data.needs_email_reply === true,
      is_urgent_legal_risk: data.is_urgent_legal_risk === true,
      key_points: Array.isArray(data.key_points) ? data.key_points : [],
    };

    return generatedTask;
  } catch (error: any) {
    if (error.name === "AbortError") {
      throw new Error("Request to n8n timed out. Please check your connection and try again.");
    }
    
    // Provide user-friendly error messages
    if (error.message.includes("fetch")) {
      throw new Error(
        `Cannot connect to n8n webhook. Please check that:\n` +
        `1. N8N_BASE_URL is configured correctly (${N8N_BASE_URL})\n` +
        `2. Your n8n instance is running and accessible\n` +
        `3. The webhook endpoint exists\n\n` +
        `Original error: ${error.message}`
      );
    }

    throw error;
  }
}

/**
 * Calls the n8n /create-task webhook to create a Salesforce task
 * 
 * This function sends the edited task data along with original email metadata
 * to n8n, which creates the task in Salesforce.
 * 
 * @param payload - Combined task and email data
 * @returns Promise<CreateTaskResult> - Response with Salesforce task ID
 * @throws Error if the API call fails
 */
export async function createSalesforceTask(payload: CreateTaskPayload): Promise<CreateTaskResult> {
  const url = `${N8N_BASE_URL}${CREATE_TASK_PATH}`;

  try {
    // Create abort controller for timeout
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), REQUEST_TIMEOUT);

    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-api-key": API_KEY,
      },
      body: JSON.stringify(payload),
      signal: controller.signal,
    });

    clearTimeout(timeoutId);

    // Check if response is OK
    if (!response.ok) {
      const errorText = await response.text().catch(() => "Unknown error");
      throw new Error(`n8n webhook returned ${response.status}: ${errorText}`);
    }

    // Parse JSON response
    const data = await response.json();

    // Validate response structure
    if (!data || typeof data !== "object") {
      throw new Error("Invalid response format from n8n webhook");
    }

    // Check status field
    if (data.status !== "ok") {
      throw new Error(data.error || data.message || "Failed to create Salesforce task");
    }

    const result: CreateTaskResult = {
      status: "ok",
      salesforceTaskId: data.salesforceTaskId || data.taskId,
      linkedContactId: data.linkedContactId,
      message: data.message,
    };

    return result;
  } catch (error: any) {
    if (error.name === "AbortError") {
      throw new Error("Request to n8n timed out. Please check your connection and try again.");
    }

    // Provide user-friendly error messages
    if (error.message.includes("fetch")) {
      throw new Error(
        `Cannot connect to n8n webhook. Please check that:\n` +
        `1. N8N_BASE_URL is configured correctly (${N8N_BASE_URL})\n` +
        `2. Your n8n instance is running and accessible\n` +
        `3. The webhook endpoint exists\n\n` +
        `Original error: ${error.message}`
      );
    }

    throw error;
  }
}

// ============================================================================
// HELPER FUNCTIONS
// ============================================================================

/**
 * Validates and normalizes the priority value
 * 
 * @param priority - Priority value from API response
 * @returns Valid TaskPriority value
 */
function validatePriority(priority: any): "Low" | "Normal" | "High" | "Urgent" {
  const validPriorities = ["Low", "Normal", "High", "Urgent"];
  
  if (typeof priority === "string" && validPriorities.includes(priority)) {
    return priority as "Low" | "Normal" | "High" | "Urgent";
  }
  
  // Default to Normal if invalid
  return "Normal";
}

/**
 * Checks if the API is configured properly
 * Returns warnings if configuration seems to be using defaults
 * 
 * @returns Array of configuration warnings
 */
export function getConfigurationWarnings(): string[] {
  const warnings: string[] = [];

  if (N8N_BASE_URL.includes("YOUR-N8N-DOMAIN")) {
    warnings.push("N8N_BASE_URL is not configured. Please update with your n8n instance URL.");
  }

  if (API_KEY === "CHANGE_ME_TO_YOUR_API_KEY") {
    warnings.push("API_KEY is not configured. Please update with your n8n webhook API key.");
  }

  return warnings;
}

