/**
 * Office.js Email Utilities
 * 
 * This module provides functions to read and extract information from the currently
 * opened email in Outlook using Office.js APIs.
 */

import { EmailInfo } from "../types";

/**
 * Maximum length for email body text to prevent huge payloads
 * Approximately 4000 characters as specified in requirements
 */
const MAX_BODY_LENGTH = 4000;

/**
 * Extracts email information from the current Outlook mail item
 * 
 * This function uses Office.js APIs to read:
 * - Subject, message ID, sent date
 * - From, To, CC, BCC addresses
 * - Plain text body (trimmed to MAX_BODY_LENGTH)
 * - Current user's mailbox address
 * 
 * @returns Promise<EmailInfo> - Structured email data
 * @throws Error if Office.js is not available or email cannot be read
 */
export async function getCurrentEmailInfo(): Promise<EmailInfo> {
  return new Promise((resolve, reject) => {
    // Ensure Office.js is initialized and mailbox is available
    if (!Office || !Office.context || !Office.context.mailbox || !Office.context.mailbox.item) {
      reject(new Error("Office.js not initialized or no mail item is selected"));
      return;
    }

    const item = Office.context.mailbox.item;
    const userProfile = Office.context.mailbox.userProfile;

    // Get the plain text body asynchronously
    item.body.getAsync(Office.CoercionType.Text, (result) => {
      if (result.status === Office.AsyncResultStatus.Failed) {
        reject(new Error(`Failed to read email body: ${result.error.message}`));
        return;
      }

      // Trim body to prevent huge payloads
      let bodyText = result.value || "";
      if (bodyText.length > MAX_BODY_LENGTH) {
        bodyText = bodyText.substring(0, MAX_BODY_LENGTH) + "\n\n[... truncated for length ...]";
      }

      // Extract email addresses from EmailAddressDetails objects
      const fromAddress = item.from?.emailAddress || "unknown@unknown.com";
      
      const toAddresses = item.to?.map((recipient) => recipient.emailAddress) || [];
      const ccAddresses = item.cc?.map((recipient) => recipient.emailAddress) || [];
      
      // BCC is not available in read mode for privacy reasons
      const bccAddresses: string[] = [];

      // Get message ID (internet message ID)
      const messageId = item.internetMessageId || "";

      // Get sent date and convert to ISO string
      const sentDate = item.dateTimeCreated || new Date();
      const sentAt = sentDate.toISOString();

      // Get current user's email address
      const outlookUser = userProfile.emailAddress;

      // Construct the EmailInfo object
      const emailInfo: EmailInfo = {
        subject: item.subject || "(No Subject)",
        bodyText: bodyText.trim(),
        from: fromAddress,
        to: toAddresses,
        cc: ccAddresses,
        bcc: bccAddresses,
        messageId: messageId,
        sentAt: sentAt,
        outlookUser: outlookUser,
      };

      resolve(emailInfo);
    });
  });
}

/**
 * Validates that Office.js is ready and a mail item is available
 * 
 * @returns boolean - true if Office context is ready
 */
export function isOfficeReady(): boolean {
  return !!(
    Office &&
    Office.context &&
    Office.context.mailbox &&
    Office.context.mailbox.item
  );
}

/**
 * Gets a short preview of the email for display purposes
 * 
 * @param emailInfo - The email information object
 * @param maxLength - Maximum length of preview (default 150)
 * @returns string - Truncated preview text
 */
export function getEmailPreview(emailInfo: EmailInfo, maxLength: number = 150): string {
  const preview = emailInfo.bodyText.replace(/\s+/g, " ").trim();
  if (preview.length <= maxLength) {
    return preview;
  }
  return preview.substring(0, maxLength) + "...";
}

