/**
 * TaskPane React Component
 * 
 * Main UI component for the Outlook add-in task pane.
 * 
 * WORKFLOW:
 * 1. On mount, read current email via Office.js
 * 2. Call n8n webhook to generate AI task suggestion
 * 3. Display editable form with task fields
 * 4. Allow user to edit and submit to create Salesforce task
 * 5. Show success/error messages with task ID
 */

import * as React from "react";
import {
  EmailInfo,
  GeneratedTask,
  CreateTaskPayload,
  CreateTaskResult,
  TaskPriority,
  UIState,
  AppError,
} from "../types";
import { getCurrentEmailInfo, getEmailPreview } from "../utils/email";
import { generateTaskFromEmail, createSalesforceTask, getConfigurationWarnings } from "../utils/api";

/**
 * Main TaskPane component
 */
export const TaskPane: React.FC = () => {
  // ========================================================================
  // STATE MANAGEMENT
  // ========================================================================

  const [uiState, setUiState] = React.useState<UIState>("loading");
  const [emailInfo, setEmailInfo] = React.useState<EmailInfo | null>(null);
  const [generatedTask, setGeneratedTask] = React.useState<GeneratedTask | null>(null);
  const [error, setError] = React.useState<AppError | null>(null);
  const [taskResult, setTaskResult] = React.useState<CreateTaskResult | null>(null);

  // Editable form fields (initialized from generatedTask)
  const [taskSubject, setTaskSubject] = React.useState("");
  const [taskDescription, setTaskDescription] = React.useState("");
  const [priority, setPriority] = React.useState<TaskPriority>("Normal");
  const [dueInDays, setDueInDays] = React.useState(3);
  const [category, setCategory] = React.useState("");
  const [needsCall, setNeedsCall] = React.useState(false);
  const [needsEmailReply, setNeedsEmailReply] = React.useState(false);
  const [isUrgentLegalRisk, setIsUrgentLegalRisk] = React.useState(false);

  // ========================================================================
  // LIFECYCLE - INITIALIZATION
  // ========================================================================

  React.useEffect(() => {
    initializeTaskPane();
  }, []);

  /**
   * Initialize the task pane:
   * 1. Read email from Office.js
   * 2. Generate task suggestion from n8n
   */
  const initializeTaskPane = async () => {
    try {
      setUiState("loading");
      setError(null);

      // Check for configuration warnings
      const warnings = getConfigurationWarnings();
      if (warnings.length > 0) {
        console.warn("Configuration warnings:", warnings);
        // Continue anyway - let the API calls fail with proper errors
      }

      // Step 1: Read email information
      const email = await getCurrentEmailInfo();
      setEmailInfo(email);

      // Step 2: Generate task suggestion from n8n
      const task = await generateTaskFromEmail(email);
      setGeneratedTask(task);

      // Step 3: Populate form fields
      setTaskSubject(task.task_subject);
      setTaskDescription(task.task_description);
      setPriority(task.priority);
      setDueInDays(task.due_in_days);
      setCategory(task.category);
      setNeedsCall(task.needs_call);
      setNeedsEmailReply(task.needs_email_reply);
      setIsUrgentLegalRisk(task.is_urgent_legal_risk);

      setUiState("editing");
    } catch (err: any) {
      console.error("Initialization error:", err);
      setError({
        message: "Failed to initialize task pane",
        details: err.message || String(err),
      });
      setUiState("error");
    }
  };

  // ========================================================================
  // FORM HANDLERS
  // ========================================================================

  /**
   * Handle "Create in Salesforce" button click
   */
  const handleCreateTask = async () => {
    if (!emailInfo) {
      setError({ message: "Email information is not available" });
      return;
    }

    try {
      setUiState("creating");
      setError(null);

      // Build payload combining task fields and email metadata
      const payload: CreateTaskPayload = {
        // Task fields
        task_subject: taskSubject,
        task_description: taskDescription,
        priority: priority,
        due_in_days: dueInDays,
        category: category,
        needs_call: needsCall,
        needs_email_reply: needsEmailReply,
        is_urgent_legal_risk: isUrgentLegalRisk,

        // Email metadata
        subject: emailInfo.subject,
        bodyText: emailInfo.bodyText,
        from: emailInfo.from,
        to: emailInfo.to,
        cc: emailInfo.cc,
        bcc: emailInfo.bcc,
        messageId: emailInfo.messageId,
        sentAt: emailInfo.sentAt,
        outlookUser: emailInfo.outlookUser,
      };

      // Call n8n to create Salesforce task
      const result = await createSalesforceTask(payload);
      setTaskResult(result);
      setUiState("success");
    } catch (err: any) {
      console.error("Create task error:", err);
      setError({
        message: "Failed to create Salesforce task",
        details: err.message || String(err),
      });
      setUiState("error");
    }
  };

  /**
   * Handle "Start Over" button click
   */
  const handleStartOver = () => {
    setTaskResult(null);
    setError(null);
    initializeTaskPane();
  };

  // ========================================================================
  // RENDER HELPERS
  // ========================================================================

  /**
   * Render loading state
   */
  const renderLoading = () => (
    <div className="loading-container">
      <div className="spinner"></div>
      <p className="loading-text">Reading email and generating task suggestion...</p>
      <p className="loading-subtext">This may take a few seconds</p>
    </div>
  );

  /**
   * Render error state
   */
  const renderError = () => (
    <div className="error-container">
      <div className="error-icon"></div>
      <h2 className="error-title">{error?.message}</h2>
      {error?.details && (
        <div className="error-details">
          <pre>{error.details}</pre>
        </div>
      )}
      <button className="btn btn-secondary" onClick={handleStartOver}>
        Try Again
      </button>
    </div>
  );

  /**
   * Render success state
   */
  const renderSuccess = () => (
    <div className="success-container">
      <div className="success-icon"></div>
      <h2 className="success-title">Task Created Successfully!</h2>
      {taskResult?.salesforceTaskId && (
        <div className="success-details">
          <p>
            <strong>Salesforce Task ID:</strong>
          </p>
          <code className="task-id">{taskResult.salesforceTaskId}</code>
        </div>
      )}
      {taskResult?.linkedContactId && (
        <div className="success-details">
          <p>
            <strong>Linked Contact ID:</strong>
          </p>
          <code className="contact-id">{taskResult.linkedContactId}</code>
        </div>
      )}
      {taskResult?.message && (
        <p className="success-message">{taskResult.message}</p>
      )}
      <button className="btn btn-primary" onClick={handleStartOver}>
        Create Another Task
      </button>
    </div>
  );

  /**
   * Render email info section (read-only)
   */
  const renderEmailInfo = () => {
    if (!emailInfo) return null;

    return (
      <div className="email-info-section">
        <h3 className="section-title">Email Information</h3>
        <div className="email-info-grid">
          <div className="email-field">
            <label>From:</label>
            <span>{emailInfo.from}</span>
          </div>
          <div className="email-field">
            <label>To:</label>
            <span>{emailInfo.to.join(", ")}</span>
          </div>
          {emailInfo.cc.length > 0 && (
            <div className="email-field">
              <label>CC:</label>
              <span>{emailInfo.cc.join(", ")}</span>
            </div>
          )}
          <div className="email-field">
            <label>Subject:</label>
            <span>{emailInfo.subject}</span>
          </div>
          <div className="email-field">
            <label>Sent:</label>
            <span>{new Date(emailInfo.sentAt).toLocaleString()}</span>
          </div>
          <div className="email-field">
            <label>Preview:</label>
            <span className="email-preview">{getEmailPreview(emailInfo)}</span>
          </div>
        </div>
      </div>
    );
  };

  /**
   * Render task form (editable)
   */
  const renderTaskForm = () => {
    if (!generatedTask) return null;

    return (
      <div className="task-form-section">
        <h3 className="section-title">AI-Suggested Salesforce Task</h3>
        <p className="section-description">Review and edit the task details before creating in Salesforce</p>

        <form className="task-form" onSubmit={(e) => e.preventDefault()}>
          {/* Task Subject */}
          <div className="form-field">
            <label htmlFor="taskSubject">Task Subject *</label>
            <input
              id="taskSubject"
              type="text"
              value={taskSubject}
              onChange={(e) => setTaskSubject(e.target.value)}
              required
            />
          </div>

          {/* Task Description */}
          <div className="form-field">
            <label htmlFor="taskDescription">Description *</label>
            <textarea
              id="taskDescription"
              value={taskDescription}
              onChange={(e) => setTaskDescription(e.target.value)}
              rows={6}
              required
            />
          </div>

          {/* Priority */}
          <div className="form-field">
            <label htmlFor="priority">Priority *</label>
            <select
              id="priority"
              value={priority}
              onChange={(e) => setPriority(e.target.value as TaskPriority)}
            >
              <option value="Low">Low</option>
              <option value="Normal">Normal</option>
              <option value="High">High</option>
              <option value="Urgent">Urgent</option>
            </select>
          </div>

          {/* Due in Days */}
          <div className="form-field">
            <label htmlFor="dueInDays">Due in Days *</label>
            <input
              id="dueInDays"
              type="number"
              value={dueInDays}
              onChange={(e) => setDueInDays(parseInt(e.target.value, 10) || 0)}
              min={0}
              max={365}
              required
            />
          </div>

          {/* Category */}
          <div className="form-field">
            <label htmlFor="category">Category</label>
            <input
              id="category"
              type="text"
              value={category}
              onChange={(e) => setCategory(e.target.value)}
              placeholder="e.g., Legal, Sales, Support"
            />
          </div>

          {/* Checkboxes */}
          <div className="form-field checkbox-field">
            <label>
              <input
                type="checkbox"
                checked={needsCall}
                onChange={(e) => setNeedsCall(e.target.checked)}
              />
              <span>Needs Call</span>
            </label>
          </div>

          <div className="form-field checkbox-field">
            <label>
              <input
                type="checkbox"
                checked={needsEmailReply}
                onChange={(e) => setNeedsEmailReply(e.target.checked)}
              />
              <span>Needs Email Reply</span>
            </label>
          </div>

          <div className="form-field checkbox-field">
            <label>
              <input
                type="checkbox"
                checked={isUrgentLegalRisk}
                onChange={(e) => setIsUrgentLegalRisk(e.target.checked)}
              />
              <span> Urgent Legal Risk</span>
            </label>
          </div>

          {/* Key Points (read-only) */}
          {generatedTask.key_points.length > 0 && (
            <div className="form-field">
              <label>Key Points Identified by AI</label>
              <ul className="key-points-list">
                {generatedTask.key_points.map((point, index) => (
                  <li key={index}>{point}</li>
                ))}
              </ul>
            </div>
          )}

          {/* Submit Button */}
          <div className="form-actions">
            <button
              type="button"
              className="btn btn-primary btn-create"
              onClick={handleCreateTask}
              disabled={!taskSubject || !taskDescription}
            >
              Create in Salesforce
            </button>
          </div>
        </form>
      </div>
    );
  };

  // ========================================================================
  // MAIN RENDER
  // ========================================================================

  return (
    <div className="task-pane">
      <header className="task-pane-header">
        <h1>LYMA Salesforce Task Creator</h1>
      </header>

      <main className="task-pane-content">
        {uiState === "loading" && renderLoading()}
        {uiState === "error" && renderError()}
        {uiState === "success" && renderSuccess()}
        {(uiState === "editing" || uiState === "creating") && (
          <>
            {renderEmailInfo()}
            {renderTaskForm()}
            {uiState === "creating" && (
              <div className="creating-overlay">
                <div className="spinner"></div>
                <p>Creating Salesforce task...</p>
              </div>
            )}
          </>
        )}
      </main>

      <footer className="task-pane-footer">
        <p>Powered by n8n AI automation</p>
      </footer>
    </div>
  );
};

