/**
 * TaskPane Entry Point
 * 
 * This file initializes the Outlook add-in task pane.
 * 
 * INITIALIZATION FLOW:
 * 1. Wait for Office.js to be ready
 * 2. Mount the React application
 * 3. Render the TaskPane component
 */

import * as React from "react";
import * as ReactDOM from "react-dom";
import { TaskPane } from "./TaskPane";
import "./TaskPane.css";

/**
 * Initialize Office.js and render the React app
 */
Office.onReady((info) => {
  console.log("Office.js ready:", info);

  // Ensure we're running in Outlook
  if (info.host === Office.HostType.Outlook) {
    console.log("Running in Outlook");
    
    // Render the React TaskPane component
    const root = document.getElementById("container");
    if (root) {
      ReactDOM.render(<TaskPane />, root);
    } else {
      console.error("Container element not found");
    }
  } else {
    console.error("This add-in is designed for Outlook only");
    
    // Show error message if not in Outlook
    const root = document.getElementById("container");
    if (root) {
      root.innerHTML = `
        <div style="padding: 20px; text-align: center;">
          <h2>Unsupported Host</h2>
          <p>This add-in is designed for Microsoft Outlook only.</p>
          <p>Current host: ${info.host}</p>
        </div>
      `;
    }
  }
});

